.main-main-wrapper {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	background: #eee;
}
.main-wrapper {
	display: grid;
	padding-top: 1.6rem;
	padding-left: 2.2rem;
	padding-right: 2.2rem;
	padding-bottom: 1.6rem;
	box-sizing: border-box;
	box-shadow: -16px 17px 45px -23px rgba(176, 176, 176, 1);
	border-radius: 0.4rem;
	width: 85vw;
	height: 85vh;
	min-width: 70%;
	margin-left: auto;
	margin-right: auto;
	background-color: white;
	grid-template-rows: 1fr 4fr;
}

.header-area-wrapper {
	display: grid;
	grid-template-columns: 50% 50%;
	margin-bottom: 2rem;
	grid-column-gap: 5rem;
}

.Searchbar-button-wrapper {
	display: grid;
	grid-template-columns: 4fr 2fr 2fr;
	justify-content: center;
	align-content: center;
	align-items: center;
}
.Name-logo-div {
	display: grid;
	grid-template-columns: 1fr 2fr;
	justify-content: center;
	align-content: center;
	align-items: center;
	justify-items: flex-start;
}

.header-area-wrapper h1 {
	font-size: 1.8rem;
	text-align: center;
	color: rgb(130, 130, 130);
	font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
	font-weight: 600;
}
.Searchbar-button-wrapper .AddLogbutton {
	display: flex;
	justify-content: center;
}
.TableStyless .actions {
	display: grid;
	justify-items: flex-end;
	text-align: end;
}

.TableStyless .Theadd {
	background-color: rgb(115, 163, 216);
}
.TableStyless .Theadd .trow .th {
	color: white;
}
thead tbody tr td {
	color: rgb(130, 130, 130);
}
.ui.button.buttonStyle {
	border-radius: 2rem;
}
